ul {
  list-style: none;
}

p {

  /* border: 1px solid #c9d3d6; */
  background-color: #1f2229;
  /* max-width: 50%; */
}
.App-list {
  /* max-width: 100%; */
  min-width: 25%;
  border-left: 5px solid rgb(146, 148, 148);
  /* background-color: #2f343d; */
  border: 1px solid;
  background-color: #1f2229;
  text-align: left;
  font-family: Arial, Helvetica, sans-serif;
  font-size: calc(5px + 1vmin);
  color:#c9d3d6;

}

.navBar {
  display:flex;
  text-align: center;
justify-content: center;
align-content: center;
/* margin-left:auto;
margin-right: auto; */
}

.buf {
  padding: 5px;
}

.btn {
  color: #dcdddd;
  border-radius: 5px;
  box-shadow: 2px 2px rgb(231, 231, 231);
  
  padding: 10px;
  border: 1px solid;
  text-decoration: none;
  background-color: rgb(28, 32, 32);
}

.Solid {
  border-top: 1px solid #fffcfc;
}
.App-list th {
  background-color:aliceblue;
  color:#1f2229
}

.App-list h3 {
  text-align: left;
}

.App-container {
  background-color: #282c34;
  min-height: 100vh;
  min-width: 25%;
  padding-left: 25%;
  padding-right: 25%;
  /* max-width: 800px; */
  /* align-self: center; */
  /* justify-self: center; */
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
  /* flex-shrink: 0; */
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
